'use client'
import React, { useEffect, useRef, useState } from 'react'
import MV from './components/MV'
import History from './components/history/History'
import Progress from './components/progress/Progress'

import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import Numbers from './components/numbers/Numbers'
import FromTheCeo from './components/fromtheceo/FromTheCeo'

gsap.registerPlugin(ScrollTrigger)

const Top = () => {
  const historyRef = useRef<HTMLDivElement | null>(null)
  const progressRef = useRef<HTMLDivElement | null>(null)

  const [activeIndex, setActiveIndex] = useState(0)
  const totalSteps = 8

  useEffect(() => {
    if (typeof window === 'undefined') return

    let ctx = gsap.context(() => {
      const historyContainer = historyRef.current
      if (historyContainer) {
        let sections = Array.from(
          historyContainer.querySelectorAll('.panel')
        ) as HTMLElement[]

        let totalWidth = sections.reduce(
          (acc, section) => acc + section.getBoundingClientRect().width,
          0
        )
        historyContainer.style.width = `${totalWidth}px`

        gsap.to(historyContainer, {
          x: () => -(totalWidth - window.innerWidth),
          ease: 'none',
          scrollTrigger: {
            trigger: historyContainer,
            pin: true,
            scrub: 1.5,
            snap: {
              snapTo: (progress) => {
                let sectionOffsets = sections.map(
                  (s) => s.offsetLeft / totalWidth
                )
                return gsap.utils.snap(sectionOffsets, progress)
              },
              duration: 0.8,
            },
            end: `+=${totalWidth - window.innerWidth}`,
          },
        })
      }

      const progressContainer = progressRef.current
      if (progressContainer) {
        let lastIndex = -1

        gsap.to(
          {},
          {
            scrollTrigger: {
              trigger: progressContainer,
              start: 'top top',
              end: `+=${totalSteps * window.innerHeight}px`,
              scrub: 3,
              pin: true,
              pinSpacing: true,
              snap: {
                snapTo: (progress) => {
                  return gsap.utils.snap(1 / (totalSteps - 1), progress)
                },
                duration: 1.2,
                ease: 'power2.out',
              },
              onUpdate: (self) => {
                const newIndex = Math.round(self.progress * (totalSteps - 1))

                if (newIndex !== lastIndex) {
                  setActiveIndex(newIndex)
                  console.log('Active Index Updated:', newIndex)
                  lastIndex = newIndex
                }
              },
            },
          }
        )
      }

      setTimeout(() => {
        ScrollTrigger.refresh()
      }, 500)
    })

    return () => ctx.revert()
  }, [])

  return (
    <section>
      <MV />
      <History ref={historyRef} />
      <Progress ref={progressRef} activeIndex={activeIndex} />
      <Numbers />
      <FromTheCeo />
    </section>
  )
}

export default Top
